import { useRecoilState, useRecoilValue } from "recoil";
import { hoursSelector, minuteState } from "../atom";
import ToDoHeader from '../toDo/ToDoHeader'
function Selectors(){
    const [minute, setMinute] = useRecoilState(minuteState);
    const [houre, setHoure] = useRecoilState(hoursSelector);
    const onChange = (e:React.FormEvent<HTMLInputElement>) => {
        setMinute(+e.currentTarget.value);
    };
    const hoursChand = (e:React.FormEvent<HTMLInputElement>) => {
        setHoure(+e.currentTarget.value)
    };

    return <div>
        <ToDoHeader/>
        <div className="container">
            <h1 className="title">Recoil Selector</h1>
            <ul>
                <li style={{margin:'40px 0'}}>
                    <label style={{display:"inline-block", width:'70px'}}> : </label><input style={{width:'calc(100% - 70px)'}} type="number" value={minute} onChange={onChange} placeholder=" ( )"/>
                    <p style={{fontSize:14, textAlign:'right'}}> {Math.floor(minute)} ==  {Math.floor(minute/60)} {Math.floor(minute%60)}</p>
                    </li>
                <li style={{margin:'40px 0'}}>
                    <label style={{display:"inline-block", width:'70px'}}> :</label><input style={{width:'calc(100% - 70px)'}} type="number" value={houre} onChange={hoursChand} placeholder=" ( )"/>
                    <p style={{fontSize:14, textAlign:'right'}}> {Math.floor(houre)} ==  {Math.floor(houre*60)}</p>
                    </li>
            </ul>
        </div>
    </div>
}

export default Selectors;