import { useRecoilState } from "recoil";
import { darkMode } from '../atom';
import { useLocation , Link} from "react-router-dom";
function CoinHeader(){
    const [dark, setDark] = useRecoilState(darkMode);
    const onClick = () => setDark((mode)=>!mode);
    const location = useLocation();
    return <header className="header">
        <nav className="flex_btw">
            {/* <Link to="/coins">{ location.pathname === '/coins' ||  location.pathname === '/coins/' ? '< ' : `< `}</Link> */}
            { location.pathname === '/coins' ||  location.pathname === '/coins/' ? <Link to="/react_mid">&lt; </Link> : <Link to="/coins">&lt; </Link>}
            <button onClick={onClick}>{dark ? '':''}</button>
        </nav>
    </header>
}

export default CoinHeader;